import Link from "next/link";
import Style from "./style.module.scss";
import { npmList, tools } from "./config";



const Tools = () => {
  return (
    <div className={Style.tools}>
      <section>
        <h3>我开发的在线工具</h3>
        <ol>
          {tools.map((item) => (
            <li key={item.path}>
              <Link href={item.path} passHref target={item.target ?? undefined}>
                {item.title}
              </Link>
            </li>
          ))}
        </ol>
      </section>
      <section>
        <h3>我的开源项目</h3>
        <ol>
          {npmList.map((item) => (
            <li key={item.path}>
              <Link href={item.path} passHref target="_blank">
                {item.title}
              </Link>
              :
              <span>{item.description}</span>
            </li>
          ))}
        </ol>
      </section>
    </div>
  );
};

export async function getStaticProps() {
  return {
    props: {},
  };
}

export default Tools;
